





































import { NIL as uuidNIL } from "uuid";

import * as Typ from "../../types/index";
import {
  mdiAccount,
  mdiPencil,
  mdiShareVariant,
  mdiDelete,
  mdiAppleKeyboardControl,
  mdiSort,
  mdiStar,
  mdiStarOutline,
  mdiStarHalfFull,
} from "@mdi/js";

import Vue from "vue";

export default Vue.extend({
  name: "ComparisionDataTableHeader",

  props: {
    criteriumKV: {
      type: Object as () => Typ.criteriumKeyValue,
    },
    sortBy: {
      type: String,
    },
    sortDesc: {
      type: Boolean,
    },
  },

  //DATA
  data() {
    return {
      uuidNIL,
      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete,
        mdiAppleKeyboardControl,
        mdiSort,
        mdiStar,
        mdiStarOutline,
        mdiStarHalfFull,
      },
      Typ,
    };
  },

  methods: {
    getCriteriumInfo(): string {
      const text =
        "Description:<br/>" +
        this.criteriumKV.value.description +
        "<br/>Importance: " +
        Typ.convertImportanceEnumToString(this.criteriumKV.value.importance) +
        "<br/>Exclusion Criterium: " +
        this.criteriumKV.value.isExclusionCriterium;

      return text;
    },
  },
});
