




























import { v4 as uuidv4 } from "uuid";
import { NIL as uuidNIL } from "uuid";

import * as Typ from "../../types/index";
import {
  mdiAccount,
  mdiPencil,
  mdiShareVariant,
  mdiDelete,
  mdiAppleKeyboardControl,
  mdiContentSaveEdit,
  mdiFileRestoreOutline,
  mdiClose,
  mdiAlert,
} from "@mdi/js";

import Vue from "vue";

export default Vue.extend({
  name: "Settings",

  props: {
    showDialog: {
      type: Boolean,
      default: false,
    },
    deleteItem: {
      type: String,
    },
    deleteFrom: {
      type: String,
    },
  },

  //DATA
  data() {
    return {
      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete,
        mdiAppleKeyboardControl,
        mdiContentSaveEdit,
        mdiFileRestoreOutline,
        mdiClose,
        mdiAlert,
      },
      Typ,
    };
  },

  methods: {
    getWarning(): string {
      return (
        "You are about to delete <br/>[" +
        this.deleteItem +
        "]<br/> from <br/>[" +
        this.deleteFrom +
        "]! <br/>Are you sure?"
      );
    },
    closeDialog(confirmed: boolean) {
      this.$emit("deletionConfirmed", confirmed);
    },
  },
});
